.App {
  width: 100%;
  height: 100%;
}
.newsfeed-header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px 15px;
  height: 46px;
  border-bottom: 1px solid rgba(255, 255, 255, 0);
  position: fixed;
  background-color: rgb(249, 249, 249);
  z-index: 108;
}

.newsfeed-header img {
  height: 26px;
  width: auto;
}

.newsfeed-post {
  margin: 0;
  padding: 0;
  margin-top: 0;
}

.newsfeed {
  margin-top: 46px;
}
