.blue-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #3897f0;
  color: white;
  border: 1px solid #3897f0;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  width: 100%;
  height: 30px;
  text-align: center;
  padding-top: 2%;
  padding-bottom: 1.6%;
}

.white-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background: 0 0;
  color: #262626;
  border: 1px solid #dbdbdb;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  width: 100%;
  text-align: center;
  padding-top: 2%;
  padding-bottom: 1.6%;
}

.large {
  height: 42px;
}
